<template>
  <base-component>
    <Breadcrumb :style="{margin: '15px 0 15px 185px', position: 'fixed'}">
      <BreadcrumbItem to="/home">首页</BreadcrumbItem>
      <BreadcrumbItem to="/clusters">DRC集群</BreadcrumbItem>
    </Breadcrumb>
    <Content class="content" :style="{padding: '10px', background: '#fff', margin: '50px 0 1px 185px', zIndex: '1'}">
      <div style="padding: 1px 1px">
        <Table stripe :columns="columns" :data="mhaGroups[current-1]">
          <template slot-scope="{ row, index }" slot="action">
            <Button type="success" size="small" style="margin-right: 5px" @click="checkConfig(row, index)">查看</Button>
            <Button type="primary" size="small" style="margin-right: 5px" @click="goToLink(row, index)">修改</Button>
            <Button type="error" size="small" style="margin-right: 5px" @click="previewRemoveConfig(row, index)">删除</Button>
            <Modal
              v-model="cluster.modal.config"
              title="DRC配置"
              width="900px">
              <Form style="width: 100%">
                <FormItem label="集群配置">
                  <Input type="textarea" :autosize="{minRows: 1,maxRows: 30}" v-model="cluster.config" readonly/>
                </FormItem>
              </Form>
            </Modal>
            <Modal
              v-model="cluster.modal.remove"
              title="删除DRC配置"
              width="900px"
              @on-ok="removeConfig"
              @on-cancel="clearRemoveConfig">
              <Form style="width: 100%">
                <FormItem label="确认删除以下双向复制吗？">
                  <Input type="textarea" :autosize="{minRows: 1,maxRows: 30}" v-model="cluster.config" readonly/>
                </FormItem>
              </Form>
            </Modal>
          </template>
        </Table>
        <div style="text-align: center;margin: 16px 0">
          <Page
            :transfer="true"
            :total="total"
            :current.sync="current"
            show-sizer
            @on-change="getMhaGroups"
            @on-page-size-change="handleChangeSize"></Page>
        </div>
      </div>
    </Content>
  </base-component>
</template>

<script>
export default {
  name: 'Application',
  data () {
    return {
      cluster: {
        config: '',
        mhaAToBeRemoved: '',
        mhaBToBeRemoved: '',
        modal: {
          config: false,
          remove: false
        }
      },
      columns: [
        {
          title: '序号',
          width: 75,
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              params.index + 1 + (this.current - 1) * this.size
            )
          }
        },
        {
          title: '集群A',
          key: 'mhaA'
        },
        {
          title: '集群B',
          key: 'mhaB'
        },
        {
          title: '状态',
          key: 'drcStatus',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          align: 'center'
        }
      ],
      mhaGroups: [],
      total: 0,
      current: 1,
      size: 10
    }
  },
  computed: {
  },
  methods: {
    getMhaGroups () {
      const that = this
      that.axios.get('/api/drc/v1/meta/groups/all')
        .then(response => {
          console.log(response)
          that.total = response.data.data.length
          let n = 0
          let temp = []
          that.mhaGroups = []
          response.data.data.forEach((item, index) => {
            if (n === that.size - 1) {
              if (item.drcEstablishStatus === 60) {
                temp.push({ mhaA: item.srcMha, mhaB: item.destMha, drcStatus: '已接入' })
              } else {
                temp.push({ mhaA: item.srcMha, mhaB: item.destMha, drcStatus: '未接入' })
              }
              that.mhaGroups.push(temp)
              n = 0
              temp = []
            } else {
              if (item.drcEstablishStatus === 60) {
                temp.push({ mhaA: item.srcMha, mhaB: item.destMha, drcStatus: '已接入' })
              } else {
                temp.push({ mhaA: item.srcMha, mhaB: item.destMha, drcStatus: '未接入' })
              }
              n++
            }
          })
          that.mhaGroups.push(temp)
        })
    },
    handleChangeSize (val) {
      this.size = val
      this.$nextTick(() => {
        this.getCluster()
      })
    },
    goToLink (row, index) {
      console.log('go to change config for ' + row.mhaA + ' and ' + row.mhaB)
      this.$router.push({ path: '/access', query: { step: '3', clustername: row.mhaA, newclustername: row.mhaB } })
    },
    checkConfig (row, index) {
      console.log(row.mhaA)
      console.log(row.mhaB)
      this.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              class: 'demo-spin-icon-load',
              props: {
                size: 18
              }
            }),
            h('div', '请稍等...')
          ])
        }
      })
      this.axios.get('/api/drc/v1/meta/config/mhas/' + row.mhaA).then(response => {
        const data = response.data.data
        console.log(data)
        this.cluster.config = data
        this.$Spin.hide()
        this.cluster.modal.config = true
      })
    },
    previewRemoveConfig (row, index) {
      console.log(row.mhaA)
      console.log(row.mhaB)
      this.cluster.mhaAToBeRemoved = row.mhaA
      this.cluster.mhaBToBeRemoved = row.mhaB
      this.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              class: 'demo-spin-icon-load',
              props: {
                size: 18
              }
            }),
            h('div', '请稍等...')
          ])
        }
      })
      this.axios.get('/api/drc/v1/meta/config/mhas/' + row.mhaA).then(response => {
        const data = response.data.data
        console.log(data)
        this.cluster.config = data
        this.$Spin.hide()
        this.cluster.modal.remove = true
      })
    },
    removeConfig () {
      console.log('mhaAToBeRemoved', this.cluster.mhaAToBeRemoved)
      console.log('mhaBToBeRemoved', this.cluster.mhaBToBeRemoved)
      this.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              class: 'demo-spin-icon-load',
              props: {
                size: 18
              }
            }),
            h('div', '请稍等...')
          ])
        }
      })
      this.axios.put('/api/drc/v1/meta/config/remove/mhas/' + this.cluster.mhaAToBeRemoved).then(response => {
        const data = response.data.data
        console.log(data)
        if (data) {
          location.reload()
        }
        this.$Spin.hide()
      })
      this.clearRemoveConfig()
    },
    clearRemoveConfig () {
      console.log('clear mhaAToBeRemoved', this.cluster.mhaAToBeRemoved)
      console.log('clear mhaBToBeRemoved', this.cluster.mhaBToBeRemoved)
      this.cluster.mhaAToBeRemoved = ''
      this.cluster.mhaBToBeRemoved = ''
    }
  },
  created () {
    this.getMhaGroups()
  }
}
</script>

